@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
html {
    --dark-teal: #1e262b;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    min-height: 100vh;
    text-align: center;
    background-color: 'HoneyDew';
}

.name {
    display: flex;
    flex-direction: column;
    font-family: 'Montserrat';
}

.name__wrapper {
    display: flex;
    justify-content: center;
}

.name__banner {
    height: 16vw;
}

.name__flame {
    height: 16vw;
}

.name__whitespace {
    min-height: 30vh;
}

.social {
    margin: 3vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.social__wrapper {
    display: grid;
    grid-template-columns: calc(3.9vw + 20px) calc(3.9vw + 20px) calc(3.9vw + 20px); 
    grid-template-rows: calc(3.9vw + 20px);
    grid-column-gap: calc(2vw + 20px);
    grid-row-gap: 20px;
}

.social__icon {
    width: calc(3.9vw + 10px);
    transition: width 0.07s;
}

.social__icon:hover {
    width: calc(3.9vw + 20px);
}

